export type Direction = 'up' | 'down' | 'left' | 'right';
// not immutable
export type Location = ImmutableMap<{x: number, y: number}>;
export type Player = ImmutableMap<{type: 'player', id: number, facing: Optional<Direction>}>;
export type Rock = ImmutableMap<{type: 'rock'}>;
// difference from rock is that player can be interposed with shiny
export type Shiny = ImmutableMap<{type: 'shiny'}>;
export type Wall = ImmutableMap<{type: 'wall'}>;
export type Enter = ImmutableMap<{type: 'enter'}>;
export type Exit = ImmutableMap<{type: 'exit'}>;
export type WormholeIn = ImmutableMap<{type: 'wormholeIn', id: number}>;
// TODO store offset here, use in equivalent_item also
export type WormholeOut = ImmutableMap<{
  type: 'wormholeOut', id: number, out_id: number, pushable: boolean,
  default_direction: Optional<Direction>,
}>;
export type Key = ImmutableMap<{type: 'key', facing: Direction}>;
export type Door = ImmutableMap<{type: 'door'}>;
export type HatchingEgg = ImmutableMap<{type: 'hatching_egg'}>;
export type Egg = ImmutableMap<{type: 'egg'}>;
export type Eggshell = ImmutableMap<{type: 'egg', flipped: boolean}>;

// export type Button = ImmutableMap<{type: 'button', id: number}>;
