export type Item = Player | Rock | Wall | Exit | WormholeIn | WormholeOut | Shiny | Egg | Eggshell;
export type ItemType =
  'player' | 'rock' | 'wall'
  | 'enter' | 'exit' | 'wormholeIn' | 'wormholeOut'
  | 'hatching_egg' | 'egg' | 'eggshell' | 'shiny' | 'key' | 'door';

export function getItemType(item: Item): ItemType {
  return (item as any).get('type');
}

export function equivalent_item(item1: Item, item2: Item): boolean {
  if (getItemType(item1) !== getItemType(item2)) {
    return false;
  }
  let t = getItemType(item1);
  if (t === 'wormholeIn') {
    return (item1 as WormholeIn).get('id') === (item2 as WormholeIn).get('id');
  }
  if (t === 'wormholeOut') {
    if ((item1 as WormholeOut).get('id') !== (item2 as WormholeOut).get('id')) {
      return false;
    }
    if ((item1 as WormholeOut).get('out_id') !== (item2 as WormholeOut).get('out_id')) {
      return false;
    }
    return true;
  }
  return true;
}

export function pushable(item: Item, unpushable?: List<Item>): boolean {
  let unpushable_list = unpushable || List<Item>();
  for (let i = 0; i < unpushable_list.size; i++) {
    if (equivalent_item(unpushable_list.get(i) as Item, item)) {
      return false;
    }
  }
  let t = getItemType(item)
  if (['shiny', 'rock', 'player', 'key', 'egg'].includes(t)) {
    return true;
  }
  if (t === 'wormholeOut') {
    return (item as WormholeOut).get('pushable');
  }
  return false;
}

// assumes item is already pushable
export function overlayable(item: Item): boolean {
  let t = getItemType(item)
  if (t === 'wormholeOut') {
    return true;
  }
  return false;
}

export type WormholeOutState = ImmutableMap<{
  out_id: number,
  base_t: number, // time the out starts existing

  // time from wormhole's point of view, at time of existence.
  // if borrowed, is unknown until debt is resolved
  // always known if there are only positive wormholes
  start_age: Optional<number>,

  delta_t: number, // offset from the in, enforce that these are unique
  location: Optional<Location>,
  nested_in: Optional<number>, // might be within another wormhole
  nested_age: Optional<number>, // age at time of entering wormhole
}>;

export type WormholeState = ImmutableMap<{
  in: Location,
  // list of outs, lowest start_age is the active one.  (at least one guaranteed to not be null)
  outs: List<WormholeOutState>,
  outPushable: boolean,
  singularity: boolean,
}>;


export function display_wormhole_in(color: string, delta: number, extra_css?: React.CSSProperties) {
  let text;
  if (delta === Infinity) {
    text = '+';
  } else if (delta > 0) {
    text = `+${delta}`
  } else if (delta < 0) {
    text = `-${-delta}`
  } else {
    text = ''
  }
  return <span className="wormholeIn" style={{
    backgroundColor: color,
    display: 'inline-block',
    ...(extra_css || {}),
  }}><span className="text">{text}</span></span>;
}

export function display_wormhole_out(color: string, delta: number, pushable: boolean, extra_css?: React.CSSProperties) {
  let text;
  if (delta < 0) {
    // in proper game, negative times should be associated with outs.  but we don't need that here
    // text = `${delta}`
    text = ''
  } else {
    text = ''
  }
  return <span className={"wormholeOut" + (pushable ? ' pushable' : '')} style={{
    borderColor: color,
    display: 'inline-block',
    // borderColor: 'white',
    ...(extra_css || {}),
  }}>{text}</span>;
}

