type ItemDisplayOptions = {
  inactive?: boolean,
  inline?: boolean,
  key?: string | number,
};
export function item_display(state: GameState, item: Item, display_opts: ItemDisplayOptions): RenderedMessage {
  if (display_opts.inline) {
    return <div style={{display: 'inline-block', height: '1em', width: '1em'}} key={display_opts.key}>
      {item_display(state, item, {...display_opts, inline: false, key: undefined})}
    </div>;
  }
  // TODO: display wormholes better?
  switch (getItemType(item)) {
    case 'player': {
      let deg: number;
      let img = 'bird-top'
      switch ((item as Player).get('facing')) {
        case 'right':
          deg = 90;
          break;
        case 'left':
          deg = 270;
          break;
        case 'up':
          deg = 0;
          break;
        case 'down':
          deg = 180;
          break;
        default:
          img = 'bird-right';
          deg = 0;
          break;
          // throw new Error(`Unknown facing: ${(item as Player).get('facing')}`);
      }
      let src = `${process.env.PUBLIC_URL + `/img/${img}.png`}`;
      return <img key={display_opts.key} className="player" alt="player" src={src} style={{
          // rotated image
          transform: 'rotate(' + deg + 'deg)',
          opacity: display_opts.inactive ? 0.6 : 1,
      }}/>
    }
    case 'rock':
      // return <div key={display_opts.key} className="rock" style={{}}/>
      return <div key={display_opts.key} className="item" style={{
          backgroundImage: `url(${process.env.PUBLIC_URL + '/img/rock.png'})`,
          width: '84%', height: '84%', margin: '8%',
      }}/>
    case 'wall':
      return <div key={display_opts.key} className="wall" style={{}}/>
    case 'exit':
      return <div key={display_opts.key} className="exit" style={{}}/>
    case 'enter':
      return <div key={display_opts.key} className="enter" style={{}}/>
    case 'wormholeIn': {
      let id = (item as WormholeIn).get('id');
      let delta_t = best_guess_delta_for_wormhole_in(state, id);
      return <React.Fragment key={display_opts.key}>
        {display_wormhole_in(
          wormhole_colors[(item as WormholeIn).get('id')],
          delta_t,
        )}
      </React.Fragment>
    }
    case 'wormholeOut': {
      let out = item as WormholeOut;
      let delta_t = state.getIn(['wormholes', out.get('id'), 'outs', out.get('out_id'), 'delta_t']) as number;
      let pushable = out.get('pushable');
      return <React.Fragment key={display_opts.key}>
        {display_wormhole_out(
          wormhole_colors[(item as WormholeOut).get('id')],
          delta_t,
          pushable,
        )}
      </React.Fragment>
    }
    case 'hatching_egg': {
      let img_url = 'hatch5'
      state.get('events').forEach((event: Event) => {
        if (getEventType(event) === 'hatch')  {
          let hatching = (event as HatchEvent).get('time') - getTime(state) + 1;
          if (hatching <= 4) {
            img_url = `hatch${5-hatching}`
          } else {
            img_url = `egg`
          }
        }
      });
      return <div key={display_opts.key} className="item egg" style={{
          backgroundImage: `url(${process.env.PUBLIC_URL + `/img/${img_url}.png`})`,
      }}/>
    }
    case 'egg':  {
      let img_url = 'egg'
      return <div key={display_opts.key} className="item egg" style={{
          backgroundImage: `url(${process.env.PUBLIC_URL + `/img/${img_url}.png`})`,
      }}/>
    }
    case 'eggshell':  {
      return <div key={display_opts.key} className="item eggshell" style={{
          backgroundImage: `url(${process.env.PUBLIC_URL + '/img/eggshell.png'})`,
          transform: (item as Eggshell).get('flipped') ? 'scaleX(-1)' : '',
      }}/>
    }
    case 'shiny': {
      /* return <div key={display_opts.key} className="shiny" style={{}}/> */
      return <div key={display_opts.key} className="item shiny" style={{
          backgroundImage: `url(${process.env.PUBLIC_URL + '/img/shiny.png'})`,
          width: '68%', height: '68%', margin: '16%',
      }}/>
      // return <div key={display_opts.key} className="shiny" style={{}}>
      //     <img src={process.env.PUBLIC_URL + '/img/bird-right-body.png'} alt="bird" style={{position: 'absolute', height: '0.6em', marginLeft: '-0.00em', marginTop: '0.3em'}}/>
      // </div>
    }
    case 'door': {
      return <div key={display_opts.key} className="door" style={{}}>
          {drawDoorSvg()}
        </div>
    }
    case 'key': {
      return <div key={display_opts.key} className="key" style={{}}>
        {drawKeySvg()}
      </div>
    }
        /*
    case 'twig':
      children.push(
        <div key={i} className="twig" style={{}}/>
      );
      break;
         */
    default:
      throw new Error(`Unknown item type: ${getItemType(item)}`);
  }
}

export function items_display(state: GameState, items: List<Item>, display_opts: ItemDisplayOptions): RenderedMessage {
  return <div className="items" style={{display: display_opts.inline ? 'inline-block': 'block'}}>
      {items.map((item, i) => {
      return item_display(state, item, {...display_opts, key: i});
      }).toArray()}
  </div>
}

export function find_positive_out(state: GameState, id: number): Optional<WormholeOutState> {
  let wormhole_state = state.get('wormholes').get(id);
  if (wormhole_state === undefined) {
    throw new Error(`no wormhole ${id}`);
  }
  // for now just assume there's at most one
  let outs = wormhole_state.get('outs').filter((out) => out.get('delta_t') >= 0);
  if (outs.size === 0) {
    return null;
  }
  if (outs.size > 1) {
    throw new Error(`multiple positive outs for wormhole ${id}`);
  }
  return outs.first();
}

export function best_guess_delta_for_wormhole_in(state: GameState, id: number): number {
  // console.log(`best_guess_delta_for_wormhole_in ${id}`);
  let wormhole_state = state.get('wormholes').get(id);
  if (wormhole_state === undefined) {
    throw new Error(`no wormhole ${id}`);
  }
  if (wormhole_state.get('singularity')) {
    return Infinity;
  }
  // let out = find_positive_out(state, id);
  // if (out === null) {
  let out = active_wormhole_out(wormhole_state);
  // }
  let delta = 0;
  let within_id = out.get('nested_in');
  if (within_id !== null) {
    // console.log(`wormhole ${id} is within wormhole ${within_id}`);
    delta += best_guess_delta_for_wormhole_in(state, within_id);
  }
  // console.log(`best_guess_delta_for_wormhole_in ${id} ${delta} ${out.get('delta_t')}`);
  return delta + out.get('delta_t');
}

// needed only for forward time travel
export function active_wormhole_out(wormhole: WormholeState): WormholeOutState {
  return (
    wormhole.get('outs')
    .filter(out => ((out.get('location') !== null) || (out.get('nested_in') !== null)))
    .filter(out => out.get('start_age') !== null)
    .get(0) // there should be exactly one
  ) as WormholeOutState;
}

export function wormhole_out_age(state: GameState, wormhole_out: WormholeOutState): Optional<number> {
  if (wormhole_out.get('nested_in') !== null) { return wormhole_out.get('nested_age'); }
  let start_age = wormhole_out.get('start_age');
  if (start_age === null) {return null;}
  return start_age + getTime(state) - wormhole_out.get('base_t');
}

export type Cell = ImmutableMap<{
  items: List<Item>,
}>;
export type Grid = List<List<Cell>>;

export type EnterWormholeEvent = ImmutableMap<{
  type: 'enter_wormhole',
  wormhole_id: number,
  time: number,
  // todo: make it possible to summon multiple items in the same timestep?
  item: Item,
  direction: Direction | null,
}>;
export type ExitWormholeEvent = ImmutableMap<{
  type: 'exit_wormhole',
  wormhole_id: number,
  wormhole_age: number,
  items: List<Item>,
  direction: Direction,
  enter_time: number,
}>;
export type ExitEvent = ImmutableMap<{
  type: 'exit',
  item: Item,
  // TODO: track wormholes by ID
  time: Optional<number>,
}>;
export type LayEvent = ImmutableMap<{
  type: 'lay',
  player_id: number,
  age: number,
}>;
export type HatchEvent = ImmutableMap<{
  type: 'hatch',
  player_id: number,
  time: number, // conceptually should be based on player age, oh well
}>;
export type Event = EnterWormholeEvent | ExitWormholeEvent | ExitEvent | LayEvent | HatchEvent; 
export type EventType = 'enter_wormhole' | 'exit_wormhole' | 'exit' | 'lay' | 'hatch';
export function getEventType(event: Event): EventType {
  return (event as any).get('type');
}

export function estimate_exit_wormhole_remaining_time(state: GameState, ex_ev: ExitWormholeEvent) {
  let wormhole_state = state.get('wormholes').get(ex_ev.get('wormhole_id'));
  if (wormhole_state === null || wormhole_state === undefined) {
    throw new Error('wormhole not found');
  }
  let age = wormhole_state.get('outs').map(out => {
    return wormhole_out_age(state, out);
  }).filter(age => age !== null).get(0);
  if (age === null || age === undefined) {
    throw new Error('active wormhole out not found');
  }
  return ex_ev.get('wormhole_age') - age + 1;
}

export type PlayerState = ImmutableMap<{
  location: Location | null,
  moved: boolean,
  age: number,
}>

export type GameState = ImmutableMap<{
  grid: Grid,
  past: List<GameState>,
  // for each player, a list of indices into the history of game states
  players: Map<number, PlayerState>,
  wormholes: Map<number, WormholeState>,
  created: boolean, // to know whether to give better error message
  events: List<Event>,
  parallel_time: number,
}>;

let USE_PARALLEL = false; // NOTE: this is probably broken now
export function existingPlayers(state: GameState): List<number> {
  return state.get('players').filter(p => p.get('location') !== null).keySeq().toList();
}

function getNextPlayerId(state: GameState): number {
  let keys = state.get('players').keySeq().toSet();
  let next_id = 0;
  while (keys.has(next_id)) {
    next_id++;
  }
  return next_id;
}

export function getTime(state: GameState): number {
  if (USE_PARALLEL) {
    return state.get('parallel_time');
  }
  return state.get('past').size;
}
export function isWon(state: GameState): boolean {
   return state.get('events').size === 0
}
// export type GameState = List<>;

export function getCell(grid: Grid, loc: Location): Cell {
  return grid.getIn([loc.get('y'), loc.get('x')]) as Cell;
}
export function updateGrid(grid: Grid, loc: Location, f: (cell: Cell) => Cell): Grid {
  return grid.updateIn([loc.get('y'), loc.get('x')], f as any);
}
export function setGrid(state: GameState, loc: Location, f: (cell: Cell) => Cell): GameState {
  return state.update('grid', (grid) => updateGrid(grid, loc, f));
}
export function updateCell(cell: Cell, f: (items: List<Item>) => List<Item>): Cell {
  return cell.update('items', (f as any));
}
export function cellUpdater(f: (items: List<Item>) => List<Item>): (cell: Cell) => Cell {
  return (cell: Cell) => updateCell(cell, f);
}

export function findCell(grid: Grid, predicate: (cell: Cell) => boolean): Optional<Location> {
  for (let y = 0; y < grid.size; y++) {
    for (let x = 0; x < (grid.get(y) as List<Cell>).size; x++) {
      const cell = grid.getIn([y, x]) as Cell;
      if (predicate(cell)) {
        return Map({ x, y }) as Location;
      }
    }
  }
  return null;
}

